import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IonicPage, NavController, NavParams,AlertController,MenuController,Events, LoadingController } from 'ionic-angular';

import { ApiProvider } from '../../providers/api/api';
import { AuthProvider } from '../../providers/auth/auth';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the HomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {
  newarraivalList:any;
  image_url:any;
  bestseller:any;
  heart=false;
  like=false;
  user_id:any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public api:ApiProvider,
    private AuthService: AuthProvider,
    public alertCtrl: AlertController,
    private service: ServiceProvider,
    private events: Events,
    private loadingCtrl: LoadingController,
  ) {
    this.user_id = AuthService.getuserid();
    this.events.publish('hideFooter', { isHidden: false});
    this.newArraival();
    this.bestSeller();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HomePage');
   
  }

  gotoDetails(id) {
  
   
    this.navCtrl.push('DetailPage', {id:id});
  }

  newArraival(){

    this.api.post('newarraival',{user_id:""}).subscribe((response : any)  => {
    //console.log(response);
   // console.log(response.products);
    if(response.ACK === 1){
      
      this.newarraivalList = response.products;
      this.image_url = response.image_url;
      //this.is_exist = 1;

      console.log(this.newarraivalList);

    }else{
      //this.message = response.msg;
      //this.is_exist = 0;
    }
    }, err => {
    this.service.popup('Alert', 'Already Registered');
    });

  }

  bestSeller(){
    
        this.api.post('bestseller',{user_id:""}).subscribe((response : any)  => {
        console.log(response);
          if(response.Ack === 1){
          
          this.bestseller = response.best_seller;
          this.image_url = "http://111.93.169.90/team4/gypsy/product_img/";
          //this.is_exist = 1;
    
          console.log(this.bestseller);
    
        }else{
          //this.message = response.msg;
          //this.is_exist = 0;
        }
        }, err => {
        this.service.popup('Alert', 'Already Registered');
        });
    
      }

  gotoviewCart() {
   // alert(111);
    this.navCtrl.push("CartPage");
  }

  addWishList(id){
    let loading = this.loadingCtrl.create({
      spinner: 'show',
      content: 'Loading...',
      duration: 3000
    });
    loading.present();
    this.api.post('addwishlist',{product_id:id,id:this.user_id}).subscribe((response : any)  => {
      console.log(response);
      if(response.Ack === 1){
        loading.dismiss();
        this.heart = true;        
      }
      else{
        this.heart = false;  
      }     
    }, err => {
      this.service.popup('Alert', 'Already Registered');
    });
  }

  addLikelLst(id){
    let loading = this.loadingCtrl.create({
      spinner: 'show',
      content: 'Loading...',
      duration: 3000
    });
    loading.present();
    this.api.post('addlike',{product_id:id,user_id:this.user_id}).subscribe((response : any)  => {
      console.log(response);
      if(response.Ack === 1){
        loading.dismiss();
        this.like = true;        
      }
      else{
        this.like = false; 
      }
    }, err => {
      this.service.popup('Alert', 'Already Registered');
    });
  }


  

}



